.fullwidth {
  max-width: 100% !important;
  width: 100% !important; }

.userinfo {
  padding-top: 30px; }

.userinfo .persona {
  text-align: center; }

.userinfo .image img {
  max-height: 150px;
  width: auto;
  display: block;
  border-width: 0 3px 0 0;
  border-style: solid; }

.userinfo .name {
  margin: 1em 0 0; }

.userinfo .email, .userinfo .phone {
  margin: 0 0 0.2em; }

.userinfo .editLink {
  font-size: 11px; }

@media print, screen and (max-width: 40em) {
  .userinfo {
    padding-left: 0 !important; }
  .courselist .course .courseDetail .action {
    position: static;
    text-align: left;
    padding-left: 15px; }
  .courselist .course .courseDetail .action button {
    margin-top: 10px; } }

.courselist {
  padding-top: 30px; }

.courselist .courseDetail {
  position: relative;
  padding: 10px; }

.courselist .courseDetail h1, .courselist .courseDetail p {
  margin: 0; }

.courselist .courseDetail .action {
  text-align: right;
  position: absolute;
  bottom: 10px;
  right: 10px; }

.courselist .courseDetail .chapter {
  padding-bottom: 7px; }

.courselist .course {
  margin-bottom: 15px !important; }

.courselist .course .ms-ProgressIndicator-itemName {
  padding: 0; }

.courselist .course .ms-ProgressIndicator-itemProgress {
  padding: 3px 0; }

.bloglist {
  margin-top: 70px; }

.bloglist .blogLink {
  padding: 0 15px 15px 0;
  margin: 0; }

.bloglist .title {
  margin: 0;
  padding: 7px; }

.coursePage {
  padding-top: 40px; }

.coursePage .heading {
  padding-bottom: 20px; }

.coursePage .heading, .coursePage .description {
  text-align: left;
  margin: 0;
  width: 80%;
  margin: 0 auto; }

.coursePage .syllabus {
  margin-top: 20px; }

.coursePage .chapter.close .allTopics {
  display: none; }

.coursePage .syllabusaccordion {
  width: 80%;
  margin: 0 auto; }

.coursePage .syllabusaccordion .title {
  padding: 10px;
  cursor: pointer; }

.allTopics .topic {
  padding-left: 10px;
  cursor: pointer;
  display: block;
  padding: 7px 15px;
  border-bottom: 1px dashed #eaeaea; }

.topic:last-of-type {
  border: none; }

.topic.active {
  font-weight: bold; }

.type-text::before {
  font-family: 'FabricMDL2Icons';
  content: '\E7C3';
  padding-right: 5px; }

.type-video::before {
  font-family: 'FabricMDL2Icons';
  content: '\E714';
  padding-right: 5px; }

.chapter-page {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: stretch;
      align-items: stretch; }

.chapter-page .details {
  padding: 20px 20px 0;
  width: 20%;
  min-width: 200px;
  height: 100vh;
  position: fixed;
  top: 60px; }

.chapter-text {
  padding: 20px 20px 0;
  margin-left: 20%; }

.chapter-text .text img {
  max-height: 200px;
  width: auto; }

.chapter-text .position {
  display: none; }

@media print, screen and (max-width: 40em) {
  .chapter-page .details {
    display: none; }
  .chapter-page .chapter-text {
    width: 90%;
    margin: 0 auto; }
  .chapter-page .chapter-text .position {
    display: block; } }



